.task-manager {
    padding-top: 5px;
    padding-left: 35px;
    padding-right: 35px;
    padding-bottom: 35px;
}

h2 {
    align-self: flex-start;
    margin-left: 1vw;
    font-size: 40px;
    font-weight: 700;
}

.task-filter-buttons {
    margin-top: -35px;
    margin-bottom: 25px;
    display: flex;
    justify-content: flex-end;
}

.task-filter-buttons button {
    padding: 10px;
    border-radius: 14px;
    height: 35px;
    width: 90px;
    cursor: pointer;
    border: 1px solid black;
    margin-top: -3px;
    margin-right: 10px;
    font-size: 12px;
    font-weight: 600;
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: space-evenly;
}

.priority-button {
    display: flex;
    align-items: center;
}

.dropdown-menu {
    position: absolute;
    width: 20%;
    height: auto;
    background-color: white;
    margin-top: 40px;
    border-radius: 12px;
    z-index: 1000;
    text-align: start;
}

.dropdown-menu ul {
    list-style: none;
    font-size: 12px;
    padding: 0;
    margin-left: 6px;
    margin-right: 6px;

}

.dropdown-menu li {
    padding: 8px;
    cursor: pointer;
    margin-bottom: 6px;
    margin-top: 6px;
}

.dropdown-menu li:last-child {
    border-bottom: none;
}

.dropdown-menu li:hover {
    background-color: #f0f0f0;
    border-radius: 7px;
    height: 100%;
}

.task-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.task-card {
    display: block;
    width: 100%;
    background-color: #f0f0f0;
    border-radius: 24px;
    padding: 30px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.05);
    color: #fff;
    overflow: hidden;
    position: relative;
    cursor: pointer;
    transition: transform 0.2s ease;
}

.task-card:hover {
    transform: scale(1.02);
}

.task-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.task-header h3 {
    font-size: 18px;
    font-weight: 800;
    margin: 0;
}

.task-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #7D8D9C;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: #fff;
    //Z index
}

.task-subtext {
    font-style: italic;
    font-size: 16px;
    margin-left: 10px;
    color: #ffffff;
    margin-top: 7px;
    font-weight: 300;
}

.task-footer {
    margin-bottom: 2px;
    margin-top: 40px;
    display: flex;
    align-content: flex-start;
    justify-content: space-between;
    font-size: 12px;
    color: #ffffff;
    z-index: 1;
}

.task-footer p {
    display: flex;
    align-items: center;
    margin: 0;
    height: fit-content;
}

.task-footer p svg {
    margin-left: 5px;
    margin-right: 5px;
    width: 16px;
    height: 16px;
}

.logoicon {
    position: absolute;
    bottom: 10px;
    right: 20px;
    z-index: 0;
}

.task-footer p {
    margin: 0;
    height: fit-content;
}

.task-card.High {
    background-color: #C49191;
}

.task-card.Medium {
    background-color: #D8AF8A;
}

.task-card.Low {
    background-color: #9DB492;
}